import React from 'react';
import './index.css';
const SearchBar = ( props ) => {
    const { activePaginationIndex, duplicateData, pageSize  }  = props;
    const startNum = ( activePaginationIndex *  pageSize ) - pageSize + 1;
    const endNum = startNum + pageSize - 1;
    const length = duplicateData.length;
    const searchLabel = `${startNum}-${endNum} of ${length}`;
    return(
        <div className='searchContainer row'>
            <div>
                <input className='searchInput' placeholder='Search by first name' 
                    onChange={props.searchNameHandler}/>
            </div>
            <div className='paginationCount'>
                { searchLabel }
            </div>
        </div>
    );
};

export default SearchBar;